export const getBeneficios = (page,token) => {


    return fetch(`https://api.klubo.club/api/page/beneficios/${page}`,{
         method: "GET",
         headers: {
            Accept: 'application/json',
            "Content-type": "application/json",
            Authorization: `Bearer ${token}`
        },
     })
     .then(response => {
         return response.json()
     })
     .catch(err => console.log(err))

 
   }


